
/* Footer main wrap */
.site-footer {
    width:100%;
    background: #2E2D25;
}

/* Footer inner wrap */
.footer-inner,
.footer-bottom-inner {
    max-width: 1200px;
    margin: auto;
}

/* footer inner top section */
.footer-inner-top {
    display: flex;
    flex-direction: row;
    padding: 80px 0;
}

/* footer inner collumns */
.footer-inner-collumn {
    width: 33%;
}

/* footer bottom copyright section */
.footer-bottom {
    width: 100%;
    background: #25241d;
    padding: 30px 0;
}

/* Footer copyright text */
.copyright-text {
    color: var(--color-white);
    font-size: 15px;
}

/* Footer left side description */
.site-description {
    color: var(--color-white);
    line-height: 24px;
    margin-bottom: 20px;
    font-size: 15px;
}

/* Footer collumns padding */
.footer-inner-collumn {
    padding-left: 10px;
    padding-right: 10px;
}


/* Footer left side contact section */
.address,
.phone,
.email,
.social-icons {
    color: var(--color-white);
    display: block;
    line-height: 25px;
    margin-bottom: 15px;
    font-size: 15px;
}

/* Footer menu headins */
.footer-menu-heading {
    color: var(--color-white);
    font-size: 16px;
    line-height: 22px;
    padding-bottom: 17px;
    text-transform: uppercase;
    position: relative;
    margin-bottom: 21px;
    font-weight:normal;
}

/* Footer menu heading bottom border */
.footer-menu-heading::before {
    background: #e73b49;
    bottom: 0;
    content: "";
    height: 2px;
    position: absolute;
    width: 40px;
}

/* Footer menu links */
.footer-menu li a {
    color: var(--color-white) !important;
    text-transform: capitalize;
    transition: all .3s;
    font-size: 15px;
}

/* Footer menu list items */
.footer-menu li {
    padding:4px 0px !important;
}

.site-title:hover,
.site-title {
    color: var(--color-white);
}


@media only screen and (max-width:768px) {
    .footer-inner-collumn {
        width: 100%;
    }
    
    .footer-bottom,
    .footer-inner-top {
        padding: 30px;
        flex-direction: column;
        row-gap: 20px;
    }

    .site-title h1 {
        font-size: 30px;
    }
    
    .footer-menu li a,
    .copyright-text,
    .contact-area p,
    .site-description {
        font-size: 13px;
    }
    
    .footer-menu-heading {
        font-size: 20px;
    }

    .footer-bottom {
        max-width: 100% !important;
        width: 85% !important;
    }
}